$easeOutExpo: cubic-bezier(0.19, 1, 0.22, 1);

html,
body {
    height: 100%;
}

body {
    position: relative;
    overflow: hidden;
    // background-color: #111;
    font-family: "helvetica neue", sans-serif;
    background-color: #1a1a1a;
    background: linear-gradient(90deg, #1a1a1a 20px, transparent 1%) 50%,
        linear-gradient(#1a1a1a 20px, transparent 1%) 50%, #222;
    background-size: 22px 22px;
    background-attachment: fixed;
    color: #fff;
    font-family: "Barlow Semi Condensed", sans-serif;
    overflow-x: hidden;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.head {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 2vw 2.5vw;
    pointer-events: none;
    z-index: 1000;

    >div {
        display: flex;
    }

    a {
        position: relative;
        color: #fff;
        text-decoration: none;
        font-size: 1rem;
        pointer-events: all;
        overflow: hidden;

        div,
        &:after {
            transition: transform 0.85s $easeOutExpo;
        }

        &:after {
            content: attr(data-txt);
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            height: 100%;
            color: pink;
        }

        &:nth-child(2) {
            margin-left: 2vw;
        }

        &:hover {

            div,
            &:after {
                transform: translateY(-100%);
            }
        }
    }
}

.dom-gl {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 1;
}

.slider {
    position: relative;
    padding: 0 22.5vw;
    display: flex;
    align-items: center;
    height: 100%;
    user-select: none;
    cursor: grab;
    z-index: 2;

    &__inner {
        display: flex;
        position: relative;
    }
}

.slide {
    overflow: hidden;

    &:first-child {
        position: relative;
    }

    &:not(:first-child) {
        position: absolute;
        top: 0;
        height: 100%;
    }

    &__inner {
        position: relative;
        overflow: hidden;
        width: 55vw;
        padding-top: 56.5%;
    }

    img {
        display: none;

        /*
    height: 100%;
    width: 140%;
    position: absolute;
    top: 0;
    left: -20%;
    object-fit: cover;
    will-change: transform;
    */
    }
}

.titles {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    pointer-events: none;
    z-index: 3;

    &__list {
        position: absolute;
        top: 0;
        left: 0;
    }

    &__title {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 6vw;
        font-weight: bold;
        letter-spacing: -0.1vw;
        color: #fff;

        &--proxy {
            visibility: hidden;
        }
    }
}

.progress {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.25rem;
    overflow: hidden;
    pointer-events: none;

    &__line {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: scaleX(0);
        transform-origin: left;
        background-color: #fff;

        &:nth-child(2) {
            transform-origin: right;
        }
    }
}

.titles__title {
    // letter-spacing: -0.0125em;
    // font-weight: 900;
    // font-size: 80px;
    // white-space: nowrap;
    // text-transform: uppercase;
    // margin-bottom: 0;
    // line-height: 1;
    // position: relative;
    // z-index: 2;
    // mix-blend-mode: difference;
    // mix-blend-mode: color-dodge;
    -webkit-text-stroke: 5px black;
    // margin: 0;
    // margin-top: 2rem;
}

